const { Model } = Backbone;

export default class Sticker extends Model {
  constructor(...args) {
    super(...args);
  }

  // Define default attributes.
  defaults() {
    return {
        title: 'No title',
        description: 'Unknown',
        liked: false,
        created: 0
      };
  }

  parse(response) {
   response.id = response._id;
   return response;
  }

  isLiked() {
    return this.get('liked');
  }

  toggle() {
    return this.set('liked', !this.isLiked());
  }

  initialize() {
    if (this.isNew()) {
      this.set('created', Date.now());
    }
  }

}


